.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

/*  - color - transparent */
.css_hr_color_to_transparent {
  margin: 2;
  padding: 0;
  border: 1;
  height: 2px;
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(18, 133, 62, 0)),
   color-stop(rgba(184, 78, 78, 0.75)), to(rgba(44, 173, 102, 0)));
  background-image: -webkit-linear-gradient(left, rgba(18, 133, 62, 0),
   rgba(184, 78, 78, 0.75), rgba(44, 173, 102, 0));
  background-image: -o-linear-gradient(left, rgba(18, 133, 62, 0),
   rgba(184, 78, 78, 0.75), rgba(44, 173, 102, 0));
  background-image: linear-gradient(to right, rgba(18, 133, 62, 0),
   rgba(184, 78, 78, 0.75), rgba(44, 173, 102, 0));
}


.css_contract_text{
    width: 100%;
    text-align: center;
    
}

.css_div_right{
  width: 85%;
  text-align: right;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



.css_input:focus{
  border:1px solid #9ac20b;
}
.css_input::-webkit-input-placeholder{
  color:rgba(148, 145, 145, 0.295);
  font-size: 0.8em
}
.css_input::-moz-input-placeholder{
  color:rgba(148, 145, 145, 0.295);
  font-size: 0.8em
}
.css_input::-ms-input-input-placeholder{
  color:rgba(148, 145, 145, 0.295);
  font-size: 0.8em
}


.css_inputbox_div{
  margin-top: 2em;
}





.css_home_button:hover {
  color: #fff;
  background-color: #899b3cce;
}

/* 
< 669css */
@media(max-width:669px){

  .css_home_button {
    border: 1;
    background-color: #dfd9d9b4;
    border-color: #caba2d;
    color: #497024;
    font-size: 10px;
    border-radius: 3px;
    padding: 10px;
  }
   .css_input{
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-size:1.4em;
    height:1.8em;
    border-radius:4px;
    border:1px solid #c8cccf;
    color:#6a6f77;
    -webkit-appearance:none;
    -moz-appearance: none;
    appearance: none;
    /* display:block;  */
    outline:0;
    padding:0.5em;
    text-decoration:none;
    width: 15em;
  }
  
   .css_search_input{
    margin: 0;
    margin-top: 0.1em;
    width: 80%;
  }

  .css_header_text_font{
    font-size: 19em;
  }

  .css_main_logo_margin_top{
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .css_home_button_search{
    margin: 10px;
    width: 30%;
  }
  
  .css_house_img{
    margin: 500px;
  }

  .css_home_button_add{
    margin: 10;
    width: 40%;
  }
}



